#header,
#main {
  display: flex;
  justify-content: center;
  padding: 30px;
}

.title {
  padding: 0 25px;
  line-height: 7px;
  font-family: 'Raleway-ExtraBold', sans-serif;
  font-size: 2.5rem;
  letter-spacing: 2px;
  border-bottom: 25px solid var(--green);
}

.personal-info-div,
.personal-contacts-div,
.personal-socials-div {
  display: flex;
}
